<!--电费收缴 / 财务确认-->
<template>
    <div style="width: 100%" class="cus-table">
        <el-table v-table-scroll style="width: 100%" :data="tableData"
            :header-cell-style="{background:'#E6EAFE',color:'#606266'}">
            <el-table-column type="index" label="序号" width="70" fixed="left" />
            <el-table-column label="所属公司" prop="company" show-overflow-tooltip width="220"></el-table-column>
            <el-table-column label="合同名称" prop="contractName" show-overflow-tooltip width="280"></el-table-column>
            <el-table-column label="合同编号" prop="contractCode" show-overflow-tooltip width="250"></el-table-column>
            <el-table-column label="账单月份" prop="billDate" width="120">
                <template slot-scope="scope">
                    <span>{{ changeMonth(scope.row['billDate']) }}</span>
                </template>
            </el-table-column>
            <el-table-column label="用电性质" prop="wkTy" width="180" show-overflow-tooltip></el-table-column>
            <el-table-column label="合同电量（kW·h）" prop="allEq" width="150"></el-table-column>
            <el-table-column label="结算起止码时间" prop="ts" width="300">
                <template slot-scope="scope">
                    <span>{{changeTime(scope.row['timeBegin'])}}-{{changeTime(scope.row['timeEnd'])}}</span>
                </template>
            </el-table-column>
            <el-table-column label="合同单价（￥/kW·h）" prop="contractPrice" width="180"></el-table-column>
            <el-table-column label="合同总价-含税（万元）" prop="totalPrice" width="180"></el-table-column>
            <el-table-column label="先锋总价-含税（万元）" prop="xfTotalPrice" width="180"></el-table-column>
            <el-table-column label="结算单" align="center" fixed="right" class-name="cusTable" width="70">
                <template slot-scope="scope">
                    <div class="table-btns">
                        <div class="center">
                            <span class="cus-btn" @click="handClick('下载',scope.row)" type="text" size="small">下载
                            </span>
                        </div>
                    </div>
                </template>
            </el-table-column>
            <el-table-column label="回款状态" width="100">
                <template slot-scope="scope">
                    <span :class="getStatus(scope.row)[1]">{{getStatus(scope.row)[0]}}</span>
                </template>
            </el-table-column>
            <el-table-column label="操作" align="center" fixed="right" class-name="cusTable" width="140">
                <template slot-scope="scope">
                    <div class="table-btns">
                        <div class="center">
                            <span v-if="isMarketer&&scope.row.klPayConfirm=='N'" class="cus-btn"
                                @click="handClick('klConfirm',scope.row)" type="text" size="small">回款确认（昆仑）
                            </span>
                            <span v-if="isFinance&&scope.row.klPayConfirm=='Y'&& scope.row.xfPayConfirm=='N'"
                                class="cus-btn" @click="handClick('xfConfirm',scope.row)" type="text"
                                size="small">回款确认（先锋）
                            </span>
                        </div>
                    </div>
                </template>
            </el-table-column>
        </el-table>
        <div class="page pagination-box">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
                :page-sizes="[10, 20, 50, 100]" :page-size="pageSize" layout="prev, pager, next, jumper,total, sizes"
                :total="total">
            </el-pagination>
        </div>
    </div>
</template>
<script>
    import {
        parseTime
    } from "@/utils/index";
    import {
        mapState
    } from 'vuex';
    import {
        ROLENAME
    } from "@/utils/config.js";
    import Table from "@/components/ElTableCommon.vue";
    export default {
        extends: Table,
        name: 'table1',
        data() {
            return {
                statusArr: ['未确认', '已确认'],
            }
        },
        computed: {
            ...mapState({ //是否是市场人员
                isMarketer: state => state.permission.roles.includes(ROLENAME.marketer),
                isFinance: state => state.permission.roles.includes(ROLENAME.finance),
            })
        },
        mounted() {},
        methods: {
            getStatus(row) {
                if (row.status == 'C') {
                    return ['回款完毕', 'green']
                } else if (row.klPayConfirm == 'N') {
                    return ['待市场确认', 'warning']
                } else if (row.xfPayConfirm == 'N') {
                    return ['待财务确认', 'blue']
                }
            },
        }
    }
</script>

<style scoped lang="scss">
    .page {
        display: flex;
        justify-content: flex-end;
    }

    .cus-btn {
        color: #2E4AF7;
        font-size: 14px;
        padding: 0 2px;
        cursor: pointer;
        line-height: 22px;
        position: relative;
    }

    .table-btns {
        display: flex;
        justify-content: space-between;

        .center {
            width: 100%;
            display: flex;
            justify-content: center;
        }

        .line:after {
            content: "";
            position: absolute;
            display: block;
            right: -12px;
            top: 6px;
            bottom: 0;
            height: 12px;
            width: 1px;
            background: #E3E5EC;
        }
    }


    /deep/ .active-row {
        background-color: #F5F7FA;
    }
</style>