<!--el-table组件的通用类-->
<script>
    import fontSize from "@/utils/fontSize"
    import {
        parseTime
    } from "@/utils/index";
    export default {
        name: 'ElTableCommon',
        props: {
            tableData: { //数据
                type: Array,
                default: () => []
            },
            total: {
                type: Number,
                default: 0
            },
            pageSize: { //分页大小
                type: Number,
                default: 10
            },
            pageNum: {
                type: Number,
                default: 1
            },
        },
        data() {
            return {
                replayId: '',
            }
        },
        methods: {
            headSpanFit(h, {
                column,
                index
            }) {
                let labelLong = column.label.length // 表头label长度
                column.minWidth = fontSize(labelLong * 0.14) + 20 // 根据label长度计算该表头最终宽度
                return h('span', {
                    class: '',
                    style: {
                        width: '100%'
                    }
                }, [column.label])
            },
            tableRowClassName({
                row,
                rowIndex
            }) {
                if (row.id == this.replayId) {
                    return 'active-row';
                }
                return '';
            },
            changeMonth(t) {
                return parseTime(new Date(t), '{y}年{m}月')
            },
            changeTime(t) {
                return parseTime(new Date(t), '{y}/{m}/{d} {h}:{i}')
            },
            handClick(btnName, row) {
                this.$emit('btnClick', btnName, row);
                this.replayId = row.id.toString(); //记录激活的行
            },
            handleCommand(item, row) {
                this.$emit('btnClick', item, row);
            },
            handleSizeChange(data) {
                this.$emit('handleSizeChange', data)
            },
            handleCurrentChange(data) {
                this.$emit('handleCurrentChange', data)
            },

        }
    }
</script>