<!--电费收缴/回款确认-->
<template>
    <BoxTitle>
        <SearchInputs @downHand="downHand" @searchHand="searchHand"></SearchInputs>
        <CusTable :tableData="tableData" @btnClick="btnClick" @handleSizeChange="handleSizeChange"
            @handleCurrentChange="handleCurrentChange" :pageNum="page.pageNum" :pageSize="page.pageSize" :total="total">
        </CusTable>
    </BoxTitle>
</template>
<script>
    //组件
    import InfoBoxBill from "./components/InfoBoxBill.vue";
    import CusTable from "./components/CostCusTable.vue";
    import SearchInputs from "./components/SearchInputsCollec.vue";
    import BoxTitle from "@/components/BoxTitle.vue";
    import {
        mapState
    } from 'vuex';
    import {
        ROLENAME
    } from "@/utils/config.js";
    //接口  
    import {
        getPayList,
        getStatisticsMonth,
        billDownload,
        exportFinalBill,
        getKlConfirm,
        getXfConfirm
    } from "@/api/archives.js";
    //公用函数
    import tool from "@/utils/tool.js";
    //组件
    import TableCommon from "@/components/TableCommon.vue";
    export default {
        extends: TableCommon,
        name: 'CostCollection',
        components: {
            CusTable,
            SearchInputs,
            BoxTitle,
            InfoBoxBill,
        },
        data() {
            return {
                data: {
                    totalPrice: 0,
                    totalEq: 0,
                    itemsEq: [], //电量统计
                    itemsMo: [], //电费统计
                },
            }
        },
        computed: {
            ...mapState({ //是否是市场人员
                isMarketer: state => state.permission.roles.includes(ROLENAME.marketer),
                userId: state => state.user.userId, //当前登录的user
            }),
        },
        mounted() {
            //获取数据 
            this.getCalc();
        },
        methods: {

            _getData(params = {}) { // 通过接口 获取最新数据 如果无接口 则是第一次加载
                getPayList({
                    ...this.search,
                    ...this.page,
                }).then(res => {
                    const data = res.data.bills.rows ? res.data.bills.rows : [];
                    this.tableData = data;
                    this.total = res.data.bills.total;
                    //统计
                    this.data.totalPrice = res.data.totalPrice;
                    this.data.totalEq = res.data.totalEq;
                })
            },
            getCalc() {
                getStatisticsMonth().then(res => {
                    if (res.data) {
                        res.data.forEach(item => {
                            this.data.itemsEq.push({
                                label: item.company,
                                num: item.totalEq,
                            })
                            this.data.itemsMo.push({
                                label: item.company,
                                num: item.totalPrice,
                            })
                        });
                    }
                })
            },
            //导出按钮
            downHand() {
                exportFinalBill({
                    ...this.search,
                    //status: 'B', //未闭环
                }).then(tool.down)
            },
            btnClick(name, row) {
                if (name == '下载') {
                    billDownload(row.id).then(res => {
                        var blob = new Blob([res]);
                        const objectUrl = URL.createObjectURL(blob);
                        //下载文件
                        const a = document.createElement('a');
                        a.setAttribute('download', '收缴资料.zip');
                        a.setAttribute('href', objectUrl);
                        a.click();
                    })
                } else if (name == 'klConfirm') {
                    this.$confirm('是否确认昆仑已收到该合同款项？', '', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        return getKlConfirm({
                            finalId: row.id,
                        })
                    }).then(res => {
                        this.success();
                        this._getData();
                    }).catch(() => {})
                } else if (name == 'xfConfirm') {
                    this.$confirm('是否确认先锋已收到该合同款项？', '', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        return getXfConfirm({
                            finalId: row.id,
                        })
                    }).then(res => {
                        this.success();
                        this._getData();
                    }).catch(() => {})
                }
            },
            success() {
                this.$message({
                    type: 'success',
                    message: '操作成功'
                })
            }
        }
    }
</script>