<template>
    <div class="search" @keyup.enter="search">
        <form-create v-model="formObj" :rule="rule" :option="options" @btn-search="search" @btn-reset="reset">
        </form-create>
        <div class="li">
            <el-button type="primary" @click="down">导 出</el-button>
        </div>
    </div>
</template>
<script>
    //接口
    import {
        getKcList,
        getContractList,
    } from "@/api/archives.js";
    import {
        parseTime,
        getPrevMonth
    } from "@/utils/index";
    import form from "../billConfirm/components/billCreate/SearchInputsCreate.vue";
    export default {
        extends: form,
        mounted() {
            this._updateKc();
            this.search();
        },
        data() {
            return {
                //表单生成规则
                rule: [{
                        type: 'select',
                        field: "kcCode",
                        title: '公司名称：',
                        value: '',
                        options: [],
                        props: {
                            filterable: true,
                        },
                        on: {
                            change: (item) => {
                                //清空平台 计量点 线路
                                const formData = this.formObj.bind();
                                formData.glCode = '';
                                if (this._findRule('kpCode')) this.formObj.exec('kpCode', 'clear')
                                if (this._findRule('jpCode')) this.formObj.exec('jpCode', 'clear')
                            }
                        },
                        col: {
                            span: 6
                        }
                    }, {
                        type: "DatePicker",
                        field: "month",
                        title: "结算月份：",
                        value: getPrevMonth(), //月份,
                        props: {
                            type: "month",
                            format: "yyyy年M月",
                            placeholder: "请选择账单月",
                            pickerOptions: {
                                disabledDate(time) {
                                    return new Date(time) > new Date().getTime()
                                }
                            },
                        },
                        col: {
                            span: 6
                        }
                    }, {
                        type: 'select',
                        field: "wkTy",
                        title: '用电性质：',
                        value: '',
                        options: [{
                            value: '',
                            label: '全部',
                        }, {
                            value: '钻井',
                            label: '钻井',
                        }, {
                            value: '压裂',
                            label: '压裂',
                        }, {
                            value: '生产',
                            label: '生产',
                        }, {
                            value: '试气',
                            label: '试气',
                        }, {
                            value: '生活',
                            label: '生活',
                        }],
                        col: {
                            span: 6
                        }
                    }, {
                        type: 'SubmitBtns',
                        field: 'btn',
                        emit: ['search', 'reset'],
                        col: {
                            span: 6
                        }
                    },
                    {
                        type: 'AutoCompleteQuery',
                        field: 'contractCode',
                        title: '合同编号：',
                        props: {
                            placeholder: '请选择合同或输入编号搜索',
                            fieldValue: 'code',
                            fieldLabel: 'code',
                            value: '',
                            queryFunc: (keyWord) => {
                                const opt = {
                                    pageNum: 1,
                                    pageSize: 20,
                                    _loading: false,
                                }
                                if (keyWord !== '') {
                                    opt.keyWord = keyWord;
                                }
                                return getContractList(opt).then(res => {
                                    if (res.data) {
                                        const list = res.data.map(item => ({
                                            ...item,
                                            value: item.code
                                        }))
                                        return list;
                                    } else {
                                        return [];
                                    }
                                })
                            }
                        },
                        col: {
                            span: 6
                        }
                    }, {
                        type: 'select',
                        field: "status",
                        title: '回款状态：',
                        value: '',
                        options: [{
                            value: '',
                            label: '全部',
                        }, {
                            value: 'B',
                            label: '待开票回款',
                        }, {
                            value: 'C',
                            label: '回款完毕',
                        }],
                        col: {
                            span: 6
                        }
                    }
                ]
            }
        },
        methods: {
            _updateGl() {}, // 覆盖父元素的方法，否则回加载线路到用电性质
            //公司列表
            _updateKc(params) {
                getKcList(params).then(data => {
                    const list = data.data.map(item => ({
                        value: item.code,
                        label: item.shortName,
                    }))
                    this.rule[0].options = [{
                        value: '',
                        label: '全部'
                    }].concat(list);
                })
            },
        }
    }
</script>