<!--InfBox组件doc-->
<!--
实用方式：  参考
<infBox :items="infoIems" title="计量点明细"></infBox>

参数说明：
title: 标题
items：{array} 数据列项数组 格式： [{ label: '计量点数量',num: 22,}],


-->

<template>
    <div class="over-box">
        <template v-for="item in items">
            <div class="title">{{item.title}}</div>
            <div class="items">
                <div v-for="item in item.list" class="item-li" :style="itemWith">
                    <span class="label">{{item.label}}：</span>
                    <span class="count">{{item.num}}</span>
                    <span class="label"> {{item.unt?item.unt:''}}</span>
                </div>
            </div>
        </template>

    </div>
</template>
<script>
    export default {
        name: 'InfBox',
        props: {
            data: {
                type: Object,
                default: () => {},
            },
        },
        mounted() {
            if (this.data) this.init(this.data)
        },
        data() {
            return {
                items: [],
            }
        },
        computed: {
            itemWith() {
                return {
                    width: (1 / 4) * 100 + '%'
                }
            }
        },
        methods: {
            init(data) {
                this.items = [{
                    title: "账单信息",
                    list: [{
                        label: '当前账单总电量',
                        num: data.totalEq,
                        unt: 'kW.h',
                    }, {
                        label: '当前账单总电费',
                        num: data.totalPrice,
                        unt: '元',
                    }]
                }, {
                    title: "当月电量（kW·h)",
                    list: data.itemsEq
                }, {
                    title: "当月电费（元)",
                    list: data.itemsMo
                }]
            },
            click(item) {
                this.$emit("click", item)
            }
        },
        watch: {
            data: {
                handler(val) {
                    if (!val) return;
                    this.init(val);
                },
                deep: true,
                immediate: true,
            },
        }
    };
</script>
<style scoped lang="scss">
    .over-box {
        background: rgba(46, 74, 247, 0.03);
        border-radius: 2px;
        border: 1px solid rgba(46, 74, 247, 0.31);
        padding: 10px 20px;
        color: #333;
        margin-bottom: 16px;

        .title {
            font-family: 'PFC'
        }
    }

    .items {
        &:last-child {
            margin-bottom: 0;
        }

        display: flex;
        align-items: center;
        margin-bottom: 16px;

        .item-li {
            line-height: 24px;
        }

        .count {
            cursor: pointer;
            text-decoration: underline;
            font-size: 14px;
            color: #000;
            font-family: "PFC"
        }
    }
</style>