<template>
    <div class="search" @keyup.enter="search">
        <form-create v-model="formObj" :rule="rule" :option="options" @btn-search="search" @btn-reset="reset"
            @kp-code-select="handleSelectKp">
        </form-create>
        <div class="li">
            <el-button type="primary" @click="down">导 出</el-button>
        </div>
    </div>
</template>
<script>
    //接口
    import {
        getGlList,
        getKpList,
        getKcList,
        getPointsList
    } from "@/api/archives.js";
    import {
        parseTime,
        getPrevMonth
    } from "@/utils/index";
    export default {
        props: {
            type: {
                type: Number,
                default: 1,
            },
            showAddBtn: Boolean, //显示新增按钮
        },
        data() {
            return {
                formObj: {},
                options: {
                    form: {
                        labelPosition: 'left',
                        labelWidth: '90px'
                    },
                    submitBtn: false,
                    resetBtn: false
                },
                //表单生成规则
                rule: [{
                    type: 'select',
                    field: "kcCode",
                    title: '公司名称：',
                    value: '',
                    options: [],
                    props: {
                        filterable: true,
                    },
                    on: {
                        change: (item) => {
                            //清空平台 计量点 线路
                            const formData = this.formObj.bind();
                            formData.glCode = '';
                            this.formObj.exec('kpCode', 'clear')
                            this.formObj.exec('jpCode', 'clear')
                        }
                    },
                    col: {
                        span: 7
                    }
                }, {
                    type: 'AutoCompleteQuery',
                    field: 'kpCode',
                    title: '平台名称：',
                    props: {
                        placeholder: '请选择平台或输入名称搜索',
                        fieldValue: 'code',
                        fieldLabel: 'name',
                        value: '',
                        queryFunc: (keyWord) => {
                            const opt = {
                                pageNum: 1,
                                pageSize: 20,
                                kcCode: this.formObj.getValue('kcCode'),
                                _loading: false, //不显示加载中图标
                            }
                            if (keyWord !== '') {
                                opt.keyWord = keyWord;
                            }
                            return getKpList(opt).then(res => {
                                if (res.data) {
                                    const list = res.data.map(item => ({
                                        ...item,
                                        value: item.name,
                                    }))
                                    return list
                                } else {
                                    return []
                                }
                            })
                        }
                    },
                    emit: ['select'],
                    // emitPrefix: 'kp',
                    col: {
                        span: 6
                    }
                }, {
                    type: 'select',
                    field: "glCode",
                    title: '线路名称：',
                    value: '',
                    options: [],
                    on: {
                        change: (item) => {
                            //清空公司 平台 计量点
                            const formData = this.formObj.bind();
                            formData.kcCode = '';
                            this.formObj.exec('kpCode', 'clear')
                            this.formObj.exec('jpCode', 'clear')
                        }
                    },
                    col: {
                        span: 6
                    }
                }, {
                    type: 'SubmitBtns',
                    field: 'btn',
                    emit: ['search', 'reset'],
                    col: {
                        span: 5
                    }
                }, {
                    type: 'AutoCompleteQuery',
                    field: 'jpCode',
                    title: '计量点名称：',
                    value: '',
                    props: {
                        placeholder: '请选择计量点或输入名称搜索',
                        fieldValue: 'code',
                        fieldLabel: 'name',
                        queryFunc: (keyWord) => {
                            const opt = {
                                pageNum: 1,
                                pageSize: 20,
                                _loading: false,
                            }
                            if (keyWord !== '') {
                                opt.jpName = keyWord;
                            }
                            const {
                                kpCode,
                                kcCode,
                                glCode
                            } = this.formObj.formData();
                            if (kpCode !== '') {
                                opt.kpCode = kpCode;
                            }
                            if (kcCode !== '') {
                                opt.kcCode = kcCode;
                            }
                            if (glCode !== '') { //线路
                                opt.glCode = glCode;
                            }
                            if (keyWord !== '') {
                                opt.keyWord = keyWord;
                            }
                            return getPointsList(opt).then(res => {
                                if (res.list) {
                                    const list = res.list.rows.map(item => ({
                                        ...item,
                                        value: item.name
                                    }))
                                    return list
                                } else {
                                    return []
                                }
                            })
                        }
                    },
                    col: {
                        span: 7
                    }
                }, {
                    type: "DatePicker",
                    field: "month",
                    title: "结算月份：",
                    value: '', //月份,
                    // value: getPrevMonth(), //月份,
                    props: {
                        type: "month",
                        format: "yyyy年M月",
                        placeholder: "请选择账单月",
                        pickerOptions: {
                            disabledDate(time) {
                                return new Date(time) > new Date().getTime()
                            }
                        },
                    },
                    col: {
                        span: 6
                    }
                }, {
                    type: 'select',
                    field: "type",
                    title: '表计类型：',
                    value: '',
                    options: [{
                        value: '',
                        label: '全部',
                    }, {
                        value: '1',
                        label: '单表',
                    }, {
                        value: '2',
                        label: '共表',
                    }, {
                        value: '3',
                        label: '合表',
                    }, {
                        value: '4',
                        label: '国网',
                    }],
                    col: {
                        span: 6
                    }
                }, {
                    type: 'ElCol',
                    props: {
                        span: 5,
                    },
                }, {
                    type: 'input',
                    field: "gcAccount",
                    title: '户　　号：',
                    value: '',
                    props: {
                        placeholder: '请输入户号',
                    },
                    col: {
                        span: 7
                    }
                }]
            }
        },
        mounted() {
            this._updateGl();
            this._updateKc();
        },
        methods: {
            //平台选择事件
            handleSelectKp(data) {
                const formData = this.formObj.bind();
                formData.glCode = '';
                if (this._findRule('jpCode')) this.formObj.exec('jpCode', 'clear')
            },
            //根据字段名称找到规则
            _findRule(type) {
                return this.rule.find(r => r.field === type);
            },
            //线路
            _updateGl(params) {
                getGlList(params).then(data => {
                    const list = data.data.map(item => ({
                        value: item.code,
                        label: item.name,
                    }))
                    this.rule[2].options = [{
                        value: '',
                        label: '全部'
                    }].concat(list);
                })
            },
            //公司列表
            _updateKc(params) {
                getKcList(params).then(data => {
                    const list = data.data.map(item => ({
                        value: item.code,
                        label: item.fullName,
                    }))
                    this.rule[0].options = [{
                        value: '',
                        label: '全部'
                    }].concat(list);
                })
            },
            down() {
                this.$emit('downHand')
            },
            search() {
                const formData = this.formObj.formData();
                const opt = {
                    ...formData,
                    month: parseTime(formData.month, '{y}-{m}'),
                }
                for (let key in opt) {
                    if (opt[key] == '' || opt[key] == undefined) delete opt[key];
                }
                this.$emit('searchHand', opt)
            },
            //重置
            reset() {
                //重置表单
                this.formObj.resetFields();
                let rules = this.formObj.model();
                //重置自定义组件
                for (let key in rules) {
                    let item = rules[key];
                    if (item.type == "AutoCompleteQuery") {
                        this.formObj.exec(item.field, 'clear')
                    }
                }
                //触发父组件方法查询
                this.$emit('searchHand', {})
            }
        }
    }
</script>

<style lang="scss">
    .search {
        padding: 24px 0 0px 0;

        .el-form-item {
            margin-bottom: 0.16rem;
        }

        .li {
            display: flex;
            margin-bottom: 16px;
        }
    }
</style>